<template>
  <div id="auth-main">
    <div id="auth-image">
      <img
        src="../assets/images/auth/auth-image.svg"
        alt="melytix"
        draggable="false"
      />
    </div>
    <div id="auth-logo">
      <img src="../assets/images/auth/logo.svg" alt="logo" draggable="false" />
    </div>
    <div id="auth-form">
      <Login />
    </div>
    <div v-if="isMessage" class="modal" @click="closeMessage">
      <span>{{ message }}</span>
    </div>
    <div v-if="isLoading" class="modal">
      <Loader />
    </div>
  </div>
</template>
<script>
import Login from "@/components/auth/Login";
import Loader from "@/components/Loader";
import { mapState, mapMutations } from "vuex";

export default {
  components: {
    Login,
    Loader
  },
  computed: {
    ...mapState("auth", ["isMessage", "message"]),
    ...mapState(["isLoading"])
  },
  methods: {
    ...mapMutations("auth", ["DEL_AUTHMESSAGE"]),
    ...mapMutations(["ON_ISLOADING", "OFF_ISLOADING"]),
    closeMessage() {
      this.DEL_AUTHMESSAGE();
    }
  }
};
</script>
<style scoped>
#auth-main {
  position: absolute;
  left: 0%;
  right: 0%;
  top: 0%;
  bottom: 0%;
  background: #0b0b0c;
  z-index: 1000;
}
#auth-image {
  position: absolute;
  left: 9.91%;
  right: 49.2%;
  top: 19.37%;
  bottom: 29.91%;
}
#auth-logo {
  position: absolute;
  left: 45.54%;
  right: 45.53%;
  top: 80.6%;
  bottom: 13.88%;
}
#auth-form {
  position: absolute;
  left: 51.73%;
  right: 16.17%;
  top: 14.82%;
  bottom: 52.62%;
}
.block-have-account {
  margin: 0.5rem 0 0 0;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: ". link link";
}
.text-have-account {
  grid-area: link;
  cursor: pointer;
  text-align: center;
  color: #7873ff;
  text-decoration: underline #7873ff;
  font-size: 0.9rem;
}
#auth-image img,
#auth-logo img {
  width: 100%;
}
.modal {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
  font-size: 5rem;
  line-height: 5rem;
  color: white;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
}
</style>
