<template>
  <form @submit.prevent="login" id="auth-form-signup">
    <h2 id="h2-form">Login</h2>
    <label for="email" id="form-email-label">Email:</label>
    <input
      id="email"
      type="email"
      v-model="authData.email"
      required
      placeholder="johnsmith@gmail.com"
    />
    <label for="password" id="form-password-label">Password:</label>
    <div id="password-block">
      <input
        id="password"
        type="password"
        v-model="authData.password"
        required
      />
      <!-- <div class="forgot-password"><span>Forgot password?</span></div> -->
    </div>
    <div class="auto-auth">
      <input
        type="checkbox"
        name="auto-auth"
        id="auto-auth-inp"
        v-model="auto_auth"
      />
      <label for="auto-auth-inp">Keep me signed in</label>
    </div>
    <div class="btn-container">
      <button id="form-btn" type="submit">Ok</button>
    </div>
    <!-- <p class="form-inputs-hints">
      Password has to be at least
      <span> 8 characters </span><br />including <span>a number </span>and
      <span>a higher case letter</span>
    </p> -->
  </form>
</template>
<script>
import { mapMutations, mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      auto_auth: false
    };
  },
  computed: {
    ...mapState("auth", ["authData", "isFetching", "isLogin"])
  },
  methods: {
    ...mapMutations("auth", ["ON_FETCHING", "OFF_FETCHING"]),
    ...mapMutations(["OPEN_SELECT", "ON_ISLOADING", "OFF_ISLOADING"]),
    ...mapActions("auth", ["LOGIN", "FORGOTPASS"]),
    async login() {
      if (!this.isFetching) {
        this.ON_ISLOADING();
        this.ON_FETCHING();
        this.LOGIN(this.auto_auth);
      }
    },
    forgotPassword(email) {
      this.FORGOTPASS(email);
    }
  }
};
</script>
<style lang="scss" scoped>
#auth-form-signup {
  display: grid;
  width: 100%;
  height: 60%;
  grid-template-rows: 1fr 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr;
  grid-template-columns: 1fr 1fr 1fr;
  grid-row-gap: 15px;
  grid-template-areas:
    "h2 h2 h2"
    "label-email email email"
    "label-password password password"
    ". auto-auth auto-auth"
    ". hints hints"
    ". btn btn";
}
#h2-form {
  grid-area: h2;
  font-size: 3vw;
  color: #ffff;
}
#form-email-label {
  grid-area: label-email;
  font-size: 2vw;
  color: #ffff;
  text-align: right;
  margin-right: 10px;
}
#form-password-label {
  grid-area: label-password;
  font-size: 2vw;
  color: #ffff;
  text-align: right;
  margin-right: 10px;
}
#password,
#email {
  background-color: #1c1c1c;
  color: white;
  border: 1px solid #1c1c1c;
  border-radius: 5px;
  height: 100%;
  font-size: 1.5vw;
}
#password {
  width: 100%;
}
#password-block {
  grid-area: password;
}
#email {
  grid-area: email;
}
#form-btn {
  outline: none;
  font-size: 1.5vw;
  padding: 0 3vw;
  color: #ffffff;
  margin: 1vw;
  cursor: pointer;
  background-color: #7873ff;
  border-radius: 0.5vw;
  transition: all 0.1s linear;
}
.form-inputs-hints {
  grid-area: hints;
  color: #ffffff;
  font-size: 0.9vw;
}
.form-inputs-hints span {
  color: #7873ff;
}
.btn-container {
  grid-area: btn;
  display: flex;
  justify-content: center;
}
#form-btn:active {
  box-shadow: inset 0 0 7px #000000;
  transform: scale(0.95);
}
#form-btn:hover {
  box-shadow: 0 0 7px #7873ff;
  cursor: pointer;
}
.forgot-password {
  position: absolute;
  margin-left: 10px;
  text-decoration: underline #7873ff;
  white-space: nowrap;
  color: #7873ff;
  font-size: 0.9vw;
  height: 3vw;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  user-select: none;
}
.forgot-password span:hover {
  cursor: pointer;
  color: #ffffff;
}
.forgot-password span:active {
  cursor: pointer;
  color: #7873ff;
}

.auto-auth {
  grid-area: auto-auth;
  display: flex;
  flex-direction: row;
  align-items: center;
  color: #7873ff;
  font-size: 0.9vw;

  label {
    margin-left: 10px;
  }
}
</style>
